<template>
  <div>
    <ContentHeader :titulo="titulo"/>

    <section class="content">
      <div class="row">
        <div class="col-sm-10 col-xs-12">
          <LineChart :data="dataSetsensor" :options="optionsChart"/>
        </div>

        <div class="col-sm-10 col-xs-12">
          <LineChart :data="dataSetsensor2" :options="optionsChart2"/>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
import ContentHeader from '@/components/dashboard/componentes/Content/ContentHeader'
import LineChart from '@/components/equipos/LineChart'
import moment from 'moment'

export default {
  name: 'MaquinaDemo',

  components: {
    ContentHeader,
    LineChart
  },

  computed: {
    dataSetsensor () {
      return {
        labels: this.tiempo1,
        datasets: [
          {
            label: 'C',
            backgroundColor: '#f87979',
            borderColor: '#f87979',
            fill: false,
            data: this.valor1
          }
        ]
      }
    },
    dataSetsensor2 () {
      return {
        labels: this.tiempo2,
        datasets: [
          {
            label: 'PSI',
            backgroundColor: '#f87979',
            borderColor: '#f87979',
            fill: false,
            data: this.valor2
          }
        ]
      }
    },
    optionsChart () {
      return { // Chart.js options
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true,
          position: 'bottom'
        },
        title: {
          display: true,
          text: 'Temperatura'
        },
        responsive: true,
        maintainAspectRatio: false
      }
    },
    optionsChart2 () {
      return { // Chart.js options
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true,
          position: 'bottom'
        },
        title: {
          display: true,
          text: 'Presion'
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },

  data () {
    return {
      tiempo1: [],
      valor1: [],
      tiempo2: [],
      valor2: [],
      titulo: 'COMPRESOR DE PRUEBA ACME'
    }
  },

  mounted () {
    setInterval(() => {
      var time = moment().format('HH:mm:ss')
      var value1 = Math.floor((Math.random() * 25) + 25)
      var value2 = Math.floor((Math.random() * 100) + 25)

      if (this.tiempo1.length > 8) {
        this.tiempo1.shift()
        this.valor1.shift()
      }

      if (this.tiempo2.length > 8) {
        this.tiempo2.shift()
        this.valor2.shift()
      }

      this.tiempo1.push(time)
      this.valor1.push(value1)
      this.tiempo2.push(time)
      this.valor2.push(value2)
    }, 3000)
  }
}
</script>
