<script>
// @ is an alias to /src
import ContentHeader from '@/components/dashboard/componentes/Content/ContentHeader'

export default {
  name: 'DemoScada',

  components: {
    ContentHeader
  },

  computed: {
    estadoValvula1In () {
      if (this.valvula1In) {
        return {
          'valvula-on': true
        }
      } else {
        return {
          'valvula-off': true
        }
      }
    },

    estadoValvula1Out () {
      if (this.valvula1Out) {
        return {
          'valvula-on': true
        }
      } else {
        return {
          'valvula-off': true
        }
      }
    },

    estadoValvula2In () {
      if (this.valvula2In) {
        return {
          'valvula-on': true
        }
      } else {
        return {
          'valvula-off': true
        }
      }
    },

    estadoValvula2Out () {
      if (this.valvula2Out) {
        return {
          'valvula-on': true
        }
      } else {
        return {
          'valvula-off': true
        }
      }
    },

    estadoBomba1 () {
      if (this.bomba1) {
        return {
          'bomba-on': true
        }
      } else {
        return {
          'bomba-off': true
        }
      }
    },

    estadoBomba2 () {
      if (this.bomba2) {
        return {
          'bomba-on': true
        }
      } else {
        return {
          'bomba-off': true
        }
      }
    },

    estadoTanque () {
      return 20 * this.tanque
    },

    alturaTanque () {
      return this.tanque + 'm'
    }
  },

  data () {
    return {
      titulo: 'Scada',
      valvula1In: true,
      valvula1Out: true,
      valvula2In: false,
      valvula2Out: false,
      bomba1: true,
      bomba2: false,
      tanque: 0
    }
  },

  mounted () {
    var ab = false
    setInterval(() => {
      if (this.tanque === 10) {
        ab = true
      }

      if (this.tanque === 0) {
        ab = false
      }

      if (this.valvula1In) {
        this.valvula1In = false
        this.valvula1Out = false
        this.bomba1 = false
        this.valvula2In = true
        this.valvula2Out = true
        this.bomba2 = true
      } else {
        this.valvula1In = true
        this.valvula1Out = true
        this.bomba1 = true
        this.valvula2In = false
        this.valvula2Out = false
        this.bomba2 = false
      }

      if (ab) {
        this.tanque--
      } else {
        this.tanque++
      }
    }, 1000)
  }
}
</script>

<style lang="css" scoped>
  svg {
    width: 100%;
    height: auto;
  }

  .valvula-off {
    fill: url(#linearGradient5956-4);
    fill-opacity: 1;
    fill-rule: evenodd;
    stroke:#000000;
    stroke-width:0.44364154;
  }

  .valvula-on {
    fill: #7FFF00;
  }

  .bomba-off {
    fill: #808080;
  }

  .bomba-on {
    fill: #7FFF00;
  }
</style>
