import openSocket from 'socket.io-client'
import store from '@/store'

const socket = openSocket('http://54.233.227.49')

const broadcasting = () => {
  socket.on('sensor', data => {
    if (!store.getters['equipo/isEmptyEquipos']) {
      store.dispatch('equipo/sensor', data.data.data)
    }
  })
}

export default broadcasting
