<template>
  <!-- <li class="active"><a href="#"><i class="fa fa-link"></i> <span>Link</span></a></li> -->
  <router-link tag="li" :to="router" v-if="type=='item'">
    <a href="#">
      <i :class="icon"></i> <span>{{ name }}</span>
    </a>
  </router-link>

  <li v-else class="treeview">
    <a href="#"><i :class="icon"></i> <span>{{ name }}</span>
      <span class="pull-right-container">
        <i class="fa fa-angle-left pull-right"></i>
      </span>
    </a>
    <ul class="treeview-menu">
      <router-link
        tag="li"
        v-for="(item, index) in items"
        :key="index"
        :to="item.router"
        v-if="item.router && item.router.name"
      >
        <a>
          <i :class="item.icon"></i> {{ item.name }}
        </a>
      </router-link>
    </ul>
  </li>
</template>

<script>
export default {

  name: 'Item',

  props: {
    type: {
      type: String,
      default: 'item'
    },
    icon: {
      type: String,
      default: ''
    },
    name: {
      type: String
    },
    items: {
      type: Array,
      default () {
        return []
      }
    },
    router: {
      type: Object,
      default () {
        return {
          name: ''
        }
      }
    }
  },

  data () {
    return {

    }
  }
}
</script>

<style lang="css" scoped>
</style>
