<template>
  <li class="dropdown user user-menu">
    <!-- Menu Toggle Button -->
    <a href="#" class="dropdown-toggle relleno" data-toggle="dropdown">
      <!-- The user image in the navbar-->
      <img src="https://almsaeedstudio.com/themes/AdminLTE/dist/img/default-50x50.gif" class="user-image" alt="User Image">
      <!-- hidden-xs hides the username on small devices so only the image appears. -->
      <span class="hidden-xs">{{ user.name }}</span>
    </a>
    <ul class="dropdown-menu">
      <!-- The user image in the menu -->
      <li class="user-header">
        <img src="https://almsaeedstudio.com/themes/AdminLTE/dist/img/default-50x50.gif" class="img-circle" alt="User Image">

        <p>
          {{ user.name }}
          <small>{{ user.rol }}</small>
        </p>
      </li>
      <!-- Menu Footer-->
      <li class="user-footer">
        <div class="pull-left">
          <a href="#" class="btn btn-default btn-flat">Archivo</a>
        </div>
        <div class="pull-right">
          <button @click="logout()" class="btn btn-default btn-flat">Salir</button>
        </div>
      </li>
    </ul>
  </li>
</template>

<script>
export default {

  name: 'UserAccountMenu',

  data () {
    return {

    }
  },
  computed: {
    user () { return this.$store.state.user.user }
  },
  methods: {
    logout () {
      this.$store.dispatch('user/logout')
    }
  }
}
</script>

<style lang="css" scoped>
  @media (min-width: 768px) {
    .relleno {
      padding-bottom: 35px;
    }
  }

</style>
