import Api from '@/services/Api'
import auth from '@/config/authentication'
import LoginException from '@/lib/exceptions/LoginException'
import store from '@/store'

export default {
  async login (credentials) {
    try {
      const response = await Api().post('/api/auth/login', credentials).catch((error) => {
        return error.response
      })

      if (response.status !== 200) {
        throw new LoginException(response.data.errors)
      }

      return {
        user: response.data.user,
        token: response.data.token
      }
    } catch (e) {
      return e
    }
  },
  async refreshToken () {
    try {
      const token = await Api().post('/oauth/token', {
        grant_type: 'refresh_token',
        refresh_token: store.getters['user/getRefreshToken'],
        client_id: auth.client_id,
        client_secret: auth.client_secret
      }).catch((error) => {
        return error.response
      })

      if (token.status !== 200) {
        throw new LoginException({
          errors: 'Falla del servicio de autentificacion'
        })
      }
      store.commit('user/setToken', token.data)
      return true
    } catch (e) {
      console.log(e)
    }
  }
}
