<template>
  <div class="login-box">
    <div class="login-logo">
      <b>Eloquent </b>LTE
    </div>
    <!-- /.login-logo -->
    <div class="login-box-body">
      <p class="login-box-msg">Iniciar secin</p>

      <div class="alert alert-danger alert-dismissible" v-show="errors">
        <i class="icon fa fa-ban"></i> Error!
        <p v-for="error in errors" :key="error.id">{{ error }}</p>
      </div>

      <form @submit.prevent="login()">
        <div class="form-group has-feedback">
          <input type="email" class="form-control" placeholder="Email" v-model="email">
          <span class="glyphicon glyphicon-envelope form-control-feedback"></span>
        </div>
        <div class="form-group has-feedback">
          <input type="password" class="form-control" placeholder="Password" v-model="password">
          <span class="glyphicon glyphicon-lock form-control-feedback"></span>
        </div>
        <div class="row">
          <div class="col-xs-4"></div>
          <!-- /.col -->
          <div class="col-xs-4">
            <button type="submit" class="btn btn-primary btn-flat">Ingresar</button>
          </div>
          <!-- /.col -->
        </div>
      </form>
    </div>
    <!-- /.login-box-body -->
  </div>
</template>

<script>
export default {

  name: 'Login',

  data () {
    return {
      email: 'gleon@gmail.com',
      password: 'guillermo',
      cerrar: false
    }
  },
  computed: {
    errors () {
      return this.$store.state.user.errors
    }
  },
  methods: {
    login () {
      this.$store.dispatch('user/login', {
        email: this.email,
        password: this.password
      })
    }
  }
}
</script>

<style lang="css">
  body {
    background-color: #d2d6de;
  }
</style>
