import AutenticationService from '@/services/Authentication'
import LoginException from '@/lib/exceptions/LoginException'
import router from '@/router'

const state = {
  user: null,
  token: null,
  isUserLoggedIn: false,
  errors: null
}

const getters = {
  isUserLoggedIn: (state) => state.isUserLoggedIn,
  getToken: (state) => {
    if (state.token) {
      // return state.token.access_token
      return state.token
    }
  },
  getRefreshToken: (state) => {
    if (state.token) {
      return state.token.refresh_token
    }
  }
}

const mutations = {
  setUser (state, user) {
    state.user = user
  },
  setToken (state, token) {
    state.token = token
    state.isUserLoggedIn = !!token
  },
  setErrors (state, errors) {
    state.errors = errors
  }
}

const actions = {
  async login ({ commit, dispatch }, credentials) {
    commit('setErrors', null)
    try {
      const reponse = await AutenticationService.login(credentials)

      if (reponse.errors) {
        throw new LoginException(reponse.errors)
      }
      commit('setUser', reponse.user)
      commit('setToken', reponse.token)
      await dispatch('equipo/initEquipos', null, { root: true })
      location.reload()
      router.push({
        name: 'home'
      })
    } catch (e) {
      commit('setErrors', e.errors)
      commit('setUser', null)
      commit('setToken', null)
    }
  },
  logout ({ commit, dispatch }) {
    commit('setUser', null)
    commit('setToken', null)
    commit('equipo/setEquipos', [], { root: true })
    router.push({
      name: 'login'
    })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
