<template>
  <div>
    <ContentHeader :titulo="titulo"/>

    <section class="content">
      <div class="row">
        <div class="col-xs-12">
          <div class="box box-primary">
            <div class="box-header with-border">
              <h3 class="box-title">Datos a consultar</h3>
            </div>
            <form>
              <div class="box-body">
                <div class="form-group col-md-4 col-xs-12">
                  <label class="col-xs-3 control-label">Fecha Inicial</label>
                  <div class="col-xs-9">
                    <VueCtkDateTimePicker
                      without-header
                      v-model="fechaInicial"
                      label=""
                      format="YYYY-MM-DD HH:mm:ssZZ"
                      time-format="HH:mm"
                    />
                  </div>
                </div>

                <div class="form-group col-md-4 col-xs-12">
                  <label class="col-xs-3 control-label">Fecha Final</label>
                  <div class="col-xs-9">
                    <VueCtkDateTimePicker
                      without-header
                      v-model="fechaFinal"
                      label=""
                      format="YYYY-MM-DD HH:mm:ssZZ"
                      time-format="HH:mm"
                    />
                  </div>
                </div>

                <div class="form-group col-md-4 col-xs-12">
                  <label class="col-xs-3 control-label">Equipo</label>
                  <div class="col-xs-9">
                    <select class="form-control" v-model="idEquipo">
                      <option v-for="equipo in listaEquipos" :value="equipo.id" :key="equipo.id">
                        {{ equipo.titulo }}
                      </option>
                    </select>
                  </div>
                </div>

                <div class="form-group col-md-4 col-xs-12">
                  <label class="col-xs-3 control-label">Sensor</label>
                  <div class="col-xs-9">
                    <select class="form-control" v-model="idSensor">
                      <option v-for="sensor in listaSensores" :value="sensor.id" :key="sensor.id">
                        {{ sensor.tipo }} {{ sensor.ubicacion }}
                      </option>
                    </select>
                  </div>
                </div>

                <div class="col-md-4 col-xs-12"></div>
                <div class="col-md-4 col-xs-12"></div>

                <div class="form-group col-md-4 col-xs-12">
                  <label class="col-xs-3 control-label">Intervalo</label>

                  <div class="col-xs-9">
                    <div class="col-xs-4 sinPadingLateral">
                      <input type="text" class="form-control" v-model="incremento">
                    </div>

                    <div class="col-xs-8 sinPadingLateral">
                      <select class="form-control" v-model="tiempo">
                        <option value="s">segundos</option>
                        <option value="m">minutos</option>
                        <option value="h">horas</option>
                      </select>
                    </div>
                  </div>

                </div>

              </div>

              <div class="box-footer">
                <button class="btn btn-primary" @click.prevent="graficar()">Consultar</button>
              </div>
            </form>
          </div>
        </div>

        <div class="col-xs-12">
          <div class="box box-primary">
            <div class="box-header with-border">
              <h3 class="box-title">Grafica</h3>
            </div>
            <div class="box-body">
              <LineChart :data="dataSetsensor" :options="optionsChart"/>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import ContentHeader from '@/components/dashboard/componentes/Content/ContentHeader'
import LineChart from '@/components/equipos/LineChart'
import Datepicker from 'vuejs-datepicker'
import VueCtkDateTimePicker from 'vue-ctk-date-time-picker'
import 'vue-ctk-date-time-picker/dist/vue-ctk-date-time-picker.min.css'

export default {
  name: 'Historico',

  components: {
    ContentHeader,
    Datepicker,
    LineChart,
    VueCtkDateTimePicker
  },

  computed: {
    listaEquipos () {
      return this.$store.getters['equipo/listaEquipos']
    },
    listaSensores () {
      return this.$store.getters['equipo/listaSensores'](this.idEquipo)
    },
    infoSensor () {
      return this.$store.getters['equipo/infoSensor'](this.idEquipo, this.idSensor)
    },
    dataSetsensor () {
      return {
        labels: this.sensor.fechas,
        datasets: [
          {
            label: this.infoSensor.tipo,
            backgroundColor: '#f87979',
            borderColor: '#f87979',
            fill: false,
            data: this.sensor.datos
          }
        ]
      }
    },
    optionsChart () {
      return { // Chart.js options
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [ {
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true,
          position: 'bottom'
        },
        title: {
          display: true,
          text: this.$store.getters['equipo/tituloSensor'](this.idEquipo, this.idSensor)
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },

  data () {
    return {
      titulo: 'Historico',
      idEquipo: '',
      idSensor: '',
      fechaInicial: '',
      fechaFinal: '',
      incremento: '',
      tiempo: '',
      sensor: {
        fechas: [],
        datos: []
      }
    }
  },

  methods: {
    async graficar () {
      try {
        var puntos = await this.$store.dispatch('equipo/getHistorySensor', {
          equipo: this.idEquipo,
          sensor: this.idSensor,
          fechaInicial: this.fechaInicial,
          fechaFinal: this.fechaFinal,
          incremento: this.incremento,
          tiempo: this.tiempo
        })

        this.sensor.fechas = puntos.fechas
        this.sensor.datos = puntos.datos
      } catch (err) {
        this.sensor.fechas = []
        this.sensor.datos = []
      }
    }
  }
}
</script>

<style lang="css" scoped>
.sinPadingLateral {
  padding-left: 0px;
  padding-right: 0px;
}
</style>
