import Vue from 'vue'
import Router from 'vue-router'
import store from './store'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Maquina1 from './views/Maquina1.vue'
import MaquinaDemo from './views/MaquinaDemo.vue'
import ScadaDemo from './views/DemoScada.vue'
import Historico from './views/Historico.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login,
      beforeEnter: (to, from, next) => {
        store.commit('user/setToken', null)
        store.commit('user/setErrors', null)
        next()
      }
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      children: [
        { path: 'maquina1', component: Maquina1 },
        { path: 'maquinaDemo', component: MaquinaDemo },
        { path: 'scadaDemo', component: ScadaDemo },
        { path: 'historico', component: Historico }
      ]
    }

    /*
    {
      path: '/about',
      name: 'about'
      route level code-splitting
      this generates a separate chunk (about.[hash].js) for this route
      which is lazy-loaded when the route is visited.
      component: () => import( webpackChunkName: "about"  './views/About.vue')
    }
    */
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path !== '/' && store.getters['user/isUserLoggedIn']) {
    next()
  } else if (to.path === '/' && store.getters['user/isUserLoggedIn']) {
    next('/home')
  } else if (to.path === '/') {
    next()
  } else {
    next('/')
  }
})

export default router
