const getters = {
  infoSensor: (state) => (idEquipo, idSensor) => {
    var equipo = _.find(state.equipos, { '_id': idEquipo })

    if (!equipo) {
      return {
        tipo: '',
        datos: [],
        fecha: []
      }
    }
    var sensor = _.find(equipo.sensores, { '_id': idSensor })

    if (!sensor) {
      return {
        tipo: '',
        datos: [],
        fecha: []
      }
    }

    return {
      tipo: sensor.tipo + ' ' + sensor.unidad,
      datos: sensor.datos,
      fecha: sensor.fecha
    }
  },
  isEmptyEquipos: (state) => {
    return state.equipos.length === 0
  },
  tituloEquipo: (state) => (idEquipo) => {
    var equipo = _.find(state.equipos, { '_id': idEquipo })

    return equipo.nombre + ' ' + equipo.marca + ' ' + equipo.modelo
  },
  tituloSensor: (state) => (idEquipo, idSensor) => {
    var equipo = _.find(state.equipos, { '_id': idEquipo })

    if (!equipo) return ''

    var sensor = _.find(equipo.sensores, { '_id': idSensor })

    if (!sensor) return ''

    return sensor.tipo + ' - ' + sensor.ubicacion
  },
  listaIdsSensores: (state) => (idEquipo) => {
    var equipo = _.find(state.equipos, { '_id': idEquipo })

    return _.map(equipo.sensores, (sensor) => {
      return sensor._id
    })
  },
  listaEquipos: (state) => {
    return _.map(state.equipos, (equipo) => {
      return {
        id: equipo._id,
        titulo: `${equipo.nombre} ${equipo.modelo} ${equipo.marca}`
      }
    })
  },
  listaSensores: (state) => (idEquipo) => {
    var equipo = _.find(state.equipos, { '_id': idEquipo })

    if (!equipo) {
      return []
    }

    return _.map(equipo.sensores, (sensor) => {
      return {
        id: sensor._id,
        tipo: sensor.tipo,
        ubicacion: sensor.ubicacion,
        unidad: sensor.unidad
      }
    })
  }
}

const mutations = {
  setEquipos (state, equipos) {
    state.equipos = equipos
  },
  inputDataSensor (state, data) {
    data.forEach((eq) => {
      var equipo = _.find(state.equipos, { '_id': eq.equipo })
      var sensor = _.find(equipo.sensores, { '_id': eq.sensor })
      var datos = sensor.datos
      var fecha = sensor.fecha

      if (datos.length > 8) {
        datos.shift()
        fecha.shift()
      }

      datos.push(eq.value)
      fecha.push(eq.time)
    })
  }
}

const actions = {
  async initEquipos ({ commit }) {
    const response = await EquipoService.equipos()
    commit('setEquipos', response.data.equipos)
  },
  sensor ({ commit }, data) {
    commit('inputDataSensor', data)
  },
  async getHistorySensor (ctx, request) {
    const response = await EquipoService.getHistorySensor(request)
    return response.data
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
