import Api from '@/services/Api'
import AutenticationService from '@/services/Authentication'

const equipos = () => {
  return Api().get('/api/equipment/index')
}

const getHistorySensor = (data) => {
  return Api().get('/api/sensor/data', {
    params: {
      equipo: data.equipo,
      sensor: data.sensor,
      fechaInicial: data.fechaInicial,
      fechaFinal: data.fechaFinal,
      incremento: data.incremento,
      tiempo: data.tiempo
    }
  }).catch(async (error) => {
    if (error.response.status === 401) {
      await AutenticationService.refreshToken()
      return getHistorySensor(data)
    }
  })
}

export default {
  equipos,
  getHistorySensor
}
